* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
}

.App {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around; /* Ensure the layout can push footer down */
  font-family: 'Montserrat', sans-serif;
  overflow-x: hidden;
  height: 100vh;
}

::-webkit-scrollbar {
  display: none;
}

.blurIn {
  animation-name: blurIn;
  animation-duration: 2s;
  animation-fill-mode: forwards;
}

@keyframes blurIn {
  from {
    filter: blur(5px);
    opacity: 0;
  }
  to {
    filter: blur(0);
    opacity: 1;
  }
}

/* Loading screen styles */
.loading-screen {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgb(255, 255, 255);
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 9999;
}

.loading-screen h1 {
  font-family: 'Montserrat', sans-serif;
  font-weight: 300;
  letter-spacing: 10px;
  color: rgb(0, 0, 0);
  font-size: 4vw;
  animation: fadeIn 4s ease-in;
}

@keyframes fadeIn {
  0% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

/* Responsive adjustments */
@media (max-width: 1024px) {
  .App {
    height: -moz-fit-content;
    height: fit-content;
  }
}

.navbar {
  display: flex;
  height: 90px;
  justify-content: center;
  width: 100vw;
  align-items: center;
  z-index: 401;
}

.navwhendrop {  height: 90px;
  display: none;
  z-index: 401;
  position: sticky;
  top: 0;
}

.navbar-content {
  display: flex;
  justify-content: space-around;
  width: 100%;
  align-items: center;
  grid-gap: 25vw;
  gap: 25vw;
  margin-top: 10px;
}

.logo {
  flex-grow: 1;
  position: absolute;
  text-align: center;
  transition: top 0.5s ease, transform 0.5s ease; 
}

.logo:hover {
  cursor: pointer;
  transform: translateY(-5px); 
}

.logo img {
  height: 10vh;
  width: auto;
}

.logo_resp {
  display: none;
  
}

.logo_resp:hover {
  cursor: pointer;
}

.logo_resp img {
  height: 7vw;
  width: auto;
}



.navbar-toggle {
  display: none;
  background-color: transparent;
  border: none;
  width: 1px;
  font-size: 30px;
  color: black;
  cursor: pointer;
  z-index: 1100;
}

.menu {
  display: none;
  z-index: 1000;
  border-radius: 10px;
  background-color: rgba(255, 255, 255, 0.43); 
  -webkit-backdrop-filter: blur(4px); 
          backdrop-filter: blur(4px); 
}

.menu.open {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  width: 100vw;
  position: absolute;
  top: 20%;
  padding: 10px;
  height: -moz-fit-content;
  height: fit-content;

  animation: bounce 0.5s ease;
}

.menu li {
  padding: 1rem;
  display: flex;
  align-items: center;
  justify-content: center;
}

.nav-links {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  font-family: 'Montserrat', sans-serif;
  font-size: 1.2vw;
  grid-gap: 20px;
  gap: 20px;
}

.nav-links li {
  text-align: center;
}

.nav-links li a {
  font-size: large;
  font-weight: 700;
  text-decoration: none;
  color: black;
  font-family: 'Montserrat', sans-serif;
  transition: color 0.3s ease;
}

.nav-links li a:hover {
  color: #0abab5;
}

.order-now {
  padding: 10px 20px;
  background-color: black;
  color: white;
  border: none;
  border-radius: 25px;
  font-size: 16px;
  font-family: 'Montserrat', sans-serif;
  cursor: pointer;  font-weight: 700;

  transition: background-color 0.3s ease;
}

.order-now:hover {
  background-color: #0abab5;
}

@keyframes bounce {
  0%, 50%, 100% {
    transform: translateY(0);
  }
  30% {
    transform: translateY(-5px);
  }
  70% {
    transform: translateY(-3px);
  }
}

@media (max-width: 1024px) {
  .navwhendrop {
    display: flex;
    justify-content: space-around;
    align-items: center;
    width: 100%;

  }

  .navbar-content {
    display: none;
  }

  .logo_resp {
    display: block;
  }
