.product-showcase {
  display: flex;
  flex-direction: row;
  justify-content: left;
  align-items: center;
  width: 150%;
  height: 80vh;
  padding: 0;
  margin: 0;
  overflow: hidden;
}

.product-showcase.expanded .section1,
.product-showcase.expanded .section2,
.product-showcase.expanded .section3 {
  transition: transform 0.5s ease;
}

.product-showcase.expanded .section1 {
  transform: translateX(-150%);
}

.product-showcase.expanded .section2 {
  z-index: 400;
  transform: translateX(0);
}

.product-showcase.expanded .section3 {
  z-index: 399;
  transform: translateX(0);
}

.section1 {
  display: flex;
  flex-direction: column;
  align-items: center;
  transform: translateX(40vw);  transition: transform 0.5s ease;

}

.section2 {
  z-index: 400;
  cursor: pointer;
  transform: translateX(50vw);  transition: transform 0.5s ease;

}

.section3 {
  width: -moz-fit-content;
  width: fit-content;
  transform: translateX(150vw);  transition: transform 0.5s ease;

}

