}

.arrow {
  background-color: transparent; /* No background */
  border: none; /* No border */
  font-size: 24px; /* Adjust size */
  cursor: pointer; /* Pointer cursor */
  z-index: 20; /* Ensure arrows are on top */
  position: absolute;
}
.arrow:hover{
  color: #0abab5;
}
.left-arrow {
  left: -40px;
}

.right-arrow {
  right: -40px;
}

.button-container {
  position: absolute; /* Position the container absolutely if needed */
  top: 2%; /* Adjust as needed */
  right: 6%; /* Adjust as needed */
}

.explore-btn {
  z-index: 500;
  border: 2px solid rgb(0, 0, 0);
  cursor: pointer;
  border-radius: 50px;
  font-weight: 700;
  font-size: 12px;
  width: 100px;
  padding: 5px 10px;
  background: transparent;
  transition: transform 0.3s ease-in-out, opacity 0.3s ease-in-out; /* Add transition for opacity */
  transform-origin: top right;
  position: relative; /* Set position relative for stacking */
}

.explore-btn:hover {
  background-color: black;
  transform: scale(1.01);
  color: white;
}

.explore-btn:focus {
  outline: none; /* Remove focus outline if undesired */
}

.explore-btn:active {
  transform: scale(0.95);
}

.btn-explore-x {
  z-index: 500;
  border: none; /* No border for the close button */
  background: transparent; /* Transparent background */
  font-size: 24px; /* Larger font for the close icon */
  position: absolute; /* Position it absolutely over the explore button */
  right: 10%; /* Adjust as needed */
}

.btn-explore-x:hover {
  transform: scale(1.05);
  color: #0abab5;
}

.prix {
  position: absolute;
  bottom: 2%;
