.leBlurIn {
  animation-name: leBlurIn;
  animation-duration: 0.2s; /* Adjust the duration as needed */
  animation-timing-function: ease-out;
}

@keyframes leBlurIn {
  from {
      transform: scaleX(0.2); /* Start with a narrow horizontal scale */
      filter: blur(20px); /* Apply a heavy blur */
      opacity: 0; /* Start invisible */
  }
  to {
      transform: scaleX(1); /* Expand to full width */
      filter: blur(0px); /* Remove the blur */
      opacity: 1; /* Fade in */
  }
}

