}

/* Intro Text */
.intro-text {
  max-width: 700px;
  margin: 1rem auto 2rem;
  font-size: 1.15rem;
  color: #555;
  font-weight: 300;
  letter-spacing: 0.5px;
  line-height: 1.8;
  font-family: 'Playfair Display', serif;
}

/* Section Styles */
.story-section,
.founder-section,
.mission-section {
  margin-top: 3rem;
  padding: 2rem;
  border-radius: 12px;
  box-shadow: 0px 10px 25px rgba(0, 0, 0, 0.15);
}



/* Subheadings */
.story-section h2,
.founder-section h2,
.mission-section h2 {
  font-size: 2.2rem;
  color: #0abab5;
  margin-bottom: 1rem;
  font-family: 'Playfair Display', serif;
}

/* Founder Section */
.founder-image {
  width: 180px;
  height: 180px;
  border-radius: 50%;
  border: 4px solid #0abab5;
  margin: 1.5rem 0;
  transition: border-color 0.3s ease;
  box-shadow: 0px 6px 15px rgba(0, 0, 0, 0.1);
}

.founder-image:hover {
  border-color: #099c98;
}

.founder-description {
  font-size: 1.2rem;
  font-weight: 300;
  color: #555;
  line-height: 1.8;
  font-family: 'Playfair Display', serif;
}

/* Italic Memory Text */
.italic p {
  font-style: italic;
  font-family: 'Playfair Display', serif;
