  font-size: 1rem;
  margin-top: 0.5rem;
}

/* Thank You Card */
.cardAU {  border: none;

  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 2rem;
}

.cta-container {
  position: relative;
  width: 320px;
  height: 200px;
  background: #333;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.7s;
  border-radius: 15px;
}

.cta-content {
  position: absolute;
  width: 100%;
  height: 100%;
  background: rgb(254, 255, 238);
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  transition: all 0.7s;
  transform: translateY(0);
  z-index: 2;
  padding: 1rem;
}

.cta-content:hover {
  transform: translateY(-100px);
  transition: all 0.7s;
}

.cta-title {
  font-size: 1.6rem;
  color: #4b5563;
  font-family: 'Playfair Display', serif;
  margin-bottom: 0.5rem;
}

.cta-text,
.cta-subtext,
.cta-footer {
  color: #4b5563;
  font-size: 0.85rem;
  font-family: 'Playfair Display', serif;
}

.cta-footer {
  padding-top: 0.5rem;
}

/* CTA Seal */
.cta-seal {
  z-index: 5;
  position: relative;
  width: 50px;
  height: 50px;
  background: #bbe8ff;
  color: #000;
  font-size: 0.75rem;
  font-weight: bold;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 4px solid #0abab5;
  clip-path: polygon(
    50% 0%, 80% 10%, 100% 35%, 100% 70%, 80% 90%, 50% 100%, 
    20% 90%, 0% 70%, 0% 35%, 20% 10%
  );
  transition: all 1s;
}

.cta-container:hover .cta-seal {
  opacity: 0;
  transform: scale(0) rotate(180deg);
}

/* Decorative Background Elements */
.cta-bg {
  position: absolute;
  width: 100%;
  height: 100%;
}

.tp {
  background: #262626;
  z-index: 3;
  clip-path: polygon(50% 50%, 100% 0, 0 0);
  transition: all 0.3s;
}

.lft {
  background: #171717;
  z-index: 3;
  clip-path: polygon(50% 50%, 0 0, 0 100%);
  transition: all 0.7s;
}

.rgt {
  background: #262626;
  z-index: 3;
  clip-path: polygon(50% 50%, 100% 0, 100% 100%);
  transition: all 0.7s;
}

.btm {
  background: #171717;
  z-index: 3;
  clip-path: polygon(50% 50%, 100% 100%, 0 100%);
  transition: all 0.7s;
}

.cta-container:hover .tp {
  clip-path: polygon(50% 0%, 100% 0, 0 0);
}

